        }
    }
}

.slide-menu a:before {
    content: '\e046';
    margin-left: 8px;
    position: relative;
    font-family: 'typicons' !important;
    opacity: 0.6;
    float: right;
}

.nav-badge {
    border-radius: 30px;
    padding: 0.4em 0.6em;
    font-size: 12px;
}

@media (max-width: 575.98px) {
